import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import swal from "sweetalert"

import rolesApi from "~/api/rolesApi"

const rolesSlice = createSlice({
    name: "roles",
    initialState: {
        status: "idle",
        roles: [],
        pagination: {
            page: 1,
            limit: 10,
            totalItem: 0,
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(getRoleList.pending, (state, action) => {
                if (state.status !== "success") {
                    state.status = "loading"
                } 
            })
            .addCase(getRoleList.fulfilled, (state, action) => {
                state.roles = action.payload.roles
                state.pagination = action.payload.pagination
                state.status = "success"
            })
            .addCase(getRoleList.rejected, (state, action) => {
                state.status = "error"
            })
            .addCase(addRole.fulfilled, (state, action) => {
                // Nu thm vai tr thnh cng
                if (action.payload.status === "OK") {
                    if (state.pagination.page === 1) {  // Kim tra nu  trang 1 th mi hin th vai tr va thm ln giao din
                        // Thc hin thm vai tr  vo u mng d liu trn redux v xa vai tr  cui mng  s vai tr trn 1 trang lun ng
                        state.roles.unshift(action.payload.data)
                        state.roles.pop()
                    }

                    // Hin th thng bo thm vai tr thnh cng
                    swal({
                        title: "Thm vai tr",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Thm vai tr",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(addRole.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Thm vai tr",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(updateRole.fulfilled, (state, action) => {
                // Nu gi request thm vai tr thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Tm kim id vai tr v thc hin cp nht thng tin mi cho vai tr 
                    state.roles.forEach((role, index, array) => {
                        if (role.id === action.payload.data.id) {
                            array[index] = action.payload.data
                        }
                    })

                    // Hin th thng bo chnh sa vai tr thnh cng
                    swal({
                        title: "Chnh sa vai tr",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Chnh sa vai tr",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(updateRole.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Chnh sa vai tr",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(deleteRole.fulfilled, (state, action) => {
                // Nu gi request xo vai tr thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Thc hin lc ra nhng vai tr c id khc vi id vai tr cn xa
                    state.roles = state.roles.filter((role) => role.id !== action.payload.data)

                    // Hin th thng bo xa vai tr thnh cng
                    swal({
                        title: "Xa vai tr",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu khng th xa vai tr
                else {
                    // Hin th cnh bo khng th xa vai tr
                    swal({
                        title: "Xa vai tr",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK ",
                    })
                }
            })
            .addCase(deleteRole.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Xa vai tr",
                    text: action.payload.message,
                    icon: "error",
                    button: "OK ",
                })
            })
    },
})
export default rolesSlice

export const getRoleList = createAsyncThunk("roles/getRoleList", async (params) => {
    const response = await rolesApi.getRoleList(params)
    return response.data.data
})
export const addRole = createAsyncThunk("roles/addRole", async (roleInfo) => {
    const response = await rolesApi.addRole(roleInfo)
    return response.data
})
export const updateRole = createAsyncThunk("roles/updateRole", async (roleInfo) => {
    const response = await rolesApi.updateRole(roleInfo)
    return response.data
})
export const deleteRole = createAsyncThunk("roles/deleteRole", async (roleId) => {
    const response = await rolesApi.deleteRole(roleId)
    return response.data
})
