import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"

import swal from "sweetalert"
import employeesApi from "~/api/employeesApi"

const employeesSlice = createSlice({
    name: "employees",
    initialState: {
        status: "idle",
        employees: [],
        pagination: {
            page: 1,
            limit: 10,
            totalItem: 0,
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(getEmployeeList.pending, (state, action) => {
                state.status = "loading"
            })
            .addCase(getEmployeeList.fulfilled, (state, action) => {
                state.employees = action.payload.employees
                state.pagination = action.payload.pagination
                state.status = "success"
            })
            .addCase(getEmployeeList.rejected, (state, action) => {
                state.status = "error"
            })
            .addCase(addEmployee.fulfilled, (state, action) => {
                // Nu thm nhn vin thnh cng
                if (action.payload.status === "OK") {
                    if (state.pagination.page === 1) {  // Kim tra nu  trang 1 th mi hin th nhn vin va thm ln giao din
                        // Thc hin thm nhn vin  vo u mng d liu trn redux v xa nhn vin  cui mng  s nhn vin trn 1 trang lun ng
                        state.employees.unshift(action.payload.data)
                        state.employees.pop()
                    }

                    // Hin th thng bo thm nhn vin thnh cng
                    swal({
                        title: "Thm nhn vin",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Thm nhn vin",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(addEmployee.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Thm nhn vin",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(updateEmployee.fulfilled, (state, action) => {
                // Nu gi request thm nhn vin thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Tm kim id nhn vin v thc hin cp nht thng tin mi cho nhn vin 
                    state.employees.forEach((employee, index, array) => {
                        if (employee.id === action.payload.data.id) {
                            array[index] = action.payload.data
                        }
                    })

                    // Hin th thng bo chnh sa nhn vin thnh cng
                    swal({
                        title: "Chnh sa nhn vin",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Chnh sa nhn vin",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(updateEmployee.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Chnh sa nhn vin",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(deleteEmployee.fulfilled, (state, action) => {
                // Nu gi request xo nhn vin thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Thc hin lc ra nhng nhn vin c id khc vi id nhn vin cn xa
                    state.employees = state.employees.filter((employee) => employee.id !== action.payload.data)

                    // Hin th thng bo xa nhn vin thnh cng
                    swal({
                        title: "Xa nhn vin",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu khng th xa nhn vin
                else {
                    // Hin th cnh bo khng th xa nhn vin
                    swal({
                        title: "Xa nhn vin",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK ",
                    })
                }
            })
            .addCase(deleteEmployee.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Xa nhn vin",
                    text: action.payload.message,
                    icon: "error",
                    button: "OK ",
                })
            })
    },
})
export default employeesSlice

export const getEmployeeList = createAsyncThunk("employees/getEmployeeList", async (params) => {
    const response = await employeesApi.getEmployeeList(params)
    return response.data.data
})
export const addEmployee = createAsyncThunk("employees/addEmployee", async (employeeInfo) => {
    const response = await employeesApi.addEmployee(employeeInfo)
    return response.data
})
export const updateEmployee = createAsyncThunk("employees/updateEmployee", async (employeeInfo) => {
    const response = await employeesApi.updateEmployee(employeeInfo)
    return response.data
})
export const deleteEmployee = createAsyncThunk("employees/deleteEmployee", async (employeeId) => {
    const response = await employeesApi.deleteEmployee(employeeId)
    return response.data
})
