import { createAsyncThunk, createSlice } from "@reduxjs/toolkit"
import swal from "sweetalert"

import teamsApi from "~/api/teamsApi"

const teamsSlice = createSlice({
    name: "teams",
    initialState: {
        status: "idle",
        teams: []
    },
    extraReducers: (builder) => {
        builder
            .addCase(getTeamList.pending, (state, action) => {
                if (state.status !== "success") {
                    state.status = "loading"
                } 
            })
            .addCase(getTeamList.fulfilled, (state, action) => {
                state.teams = action.payload
                state.status = "success"
            })
            .addCase(getTeamList.rejected, (state, action) => {
                state.status = "error"
            })
            .addCase(addTeam.fulfilled, (state, action) => {
                // Nu thm i nhm thnh cng
                if (action.payload.status === "OK") {
                        state.teams.push(action.payload.data)

                    // Hin th thng bo thm i nhm thnh cng
                    swal({
                        title: "Thm i nhm",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Thm i nhm",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(addTeam.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Thm i nhm",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(updateTeam.fulfilled, (state, action) => {
                // Nu gi request thm i nhm thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Tm kim id i nhm v thc hin cp nht thng tin mi cho i nhm 
                    state.teams.forEach((team, index, array) => {
                        if (team.id === action.payload.id) {
                            array[index] = action.payload
                        }
                    })

                    // Hin th thng bo chnh sa i nhm thnh cng
                    swal({
                        title: "Chnh sa i nhm",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu c d liu khng hp l
                else {
                    // Hin th thng bo d liu thng hp l
                    swal({
                        title: "Chnh sa i nhm",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK",
                    })
                }
            })
            .addCase(updateTeam.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Chnh sa i nhm",
                    text: action.error.message,
                    icon: "error",
                    button: "OK",
                })
            })
            .addCase(deleteTeam.fulfilled, (state, action) => {
                // Nu gi request xo i nhm thnh cng ln Server
                if (action.payload.status === "OK") {
                    // Thc hin lc ra nhng i nhm c id khc vi id i nhm cn xa
                    state.teams = state.teams.filter((team) => team.id !== action.payload.data)

                    // Hin th thng bo xa i nhm thnh cng
                    swal({
                        title: "Xa i nhm",
                        text: action.payload.message,
                        icon: "success",
                        button: "OK",
                    })
                }

                // Nu khng th xa i nhm
                else {
                    // Hin th cnh bo khng th xa i nhm
                    swal({
                        title: "Xa i nhm",
                        text: action.payload.message,
                        icon: "warning",
                        button: "OK ",
                    })
                }
            })
            .addCase(deleteTeam.rejected, (state, action) => {
                // Hin th thng bo nu gi request tht bi
                swal({
                    title: "Xa i nhm",
                    text: action.payload.message,
                    icon: "error",
                    button: "OK ",
                })
            })
    },
})
export default teamsSlice

export const getTeamList = createAsyncThunk("teams/getTeamList", async (params) => {
    const response = await teamsApi.getTeamList(params)
    return response.data.data
})
export const addTeam = createAsyncThunk("teams/addTeam", async (teamInfo) => {
    const response = await teamsApi.addTeam(teamInfo)
    return response.data
})
export const updateTeam = createAsyncThunk("teams/updateTeam", async (teamInfo) => {
    const response = await teamsApi.updateTeam(teamInfo)
    return response.data
})
export const deleteTeam = createAsyncThunk("teams/deleteTeam", async (teamId) => {
    const response = await teamsApi.deleteTeam(teamId)
    return response.data
})
