import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

import swal from "sweetalert";
import postsApi from "~/api/postsApi";

const postsSlice = createSlice({
  name: "posts",
  initialState: {
    status: "idle",
    posts: [],
    pagination: {
      page: 1,
      limit: 10,
      totalItem: 0,
    },
    nameFileUpload:""
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchPosts.pending, (state, action) => {
        if (state.status !== "success") {
          state.status = "loading";
        }
      })
      .addCase(fetchPosts.fulfilled, (state, action) => {
        state.posts = action.payload;
        state.pagination = action.payload.pagination;
        state.status = "success";
      })
      .addCase(fetchPosts.rejected, (state, action) => {
        state.status = "error";
      })
      .addCase(addPost.fulfilled, (state, action) => {
        // Nu thm bi vit thnh cng
        if (action.payload.status === "OK") {
          // if (state.pagination.page === 1) {
            // Kim tra nu  trang 1 th mi hin th bi vit va thm ln giao din
            // Thc hin thm bi vit  vo u mng d liu trn redux v xa bi vit  cui mng  s bi vit trn 1 trang lun ng
            state.posts.unshift(action.payload.data);
            // state.posts.pop();
            // console.log(state.posts)
          // }

          // Hin th thng bo thm bi vit thnh cng
          swal({
            title: "Thm bi vit",
            text: action.payload.message,
            icon: "success",
            button: "OK",
          });
        }

        // Nu c d liu khng hp l
        else {
          // Hin th thng bo d liu thng hp l
          swal({
            title: "Thm bi vit",
            text: action.payload.message,
            icon: "warning",
            button: "OK",
          });
        }
      })
      .addCase(addPost.rejected, (state, action) => {
        // Hin th thng bo nu gi request tht bi
        swal({
          title: "Thm bi vit",
          text: action.error.message,
          icon: "error",
          button: "OK",
        });
      })
      .addCase(updatePost.fulfilled, (state, action) => {
        // Nu gi request thm bi vit thnh cng ln Server
        if (action.payload[0].status === "OK") {
          // Tm kim id bi vit v thc hin cp nht thng tin mi cho bi vit 
          state.posts.forEach((post, index, array) => {
            if (post.id === action.payload[1]) {
              array[index] = action.payload[0].data; 
            }
          });

          // Hin th thng bo chnh sa bi vit thnh cng
          swal({
            title: "Chnh sa bi vit",
            text: action.payload.message,
            icon: "success",
            button: "OK",
          });
        }

        // Nu c d liu khng hp l
        else {
          // Hin th thng bo d liu thng hp l
          swal({
            title: "Chnh sa bi vit",
            text: action.payload.message,
            icon: "warning",
            button: "OK",
          });
        }
      })
      .addCase(updatePost.rejected, (state, action) => {
        // Hin th thng bo nu gi request tht bi
        swal({
          title: "Chnh sa bi vit",
          text: action.error.message,
          icon: "error",
          button: "OK",
        });
      })
      .addCase(deletePost.fulfilled, (state, action) => {
        // Nu gi request xo bi vit thnh cng ln Server
        if (action.payload[0].status === "OK") {
          // Thc hin lc ra nhng bi vit c id khc vi id bi vit cn xa
          state.posts = state.posts.filter(
            (post) => post.id !== action.payload[1]
          );

          // Hin th thng bo xa bi vit thnh cng
          swal({
            title: "Xa bi vit",
            text: action.payload.message,
            icon: "success",
            button: "OK",
          });
        }

        // Nu khng th xa bi vit
        else {
          // Hin th cnh bo khng th xa bi vit
          swal({
            title: "Xa bi vit",
            text: action.payload.message,
            icon: "warning",
            button: "OK ",
          });
        }
      })
      .addCase(deletePost.rejected, (state, action) => {
        // Hin th thng bo nu gi request tht bi
        swal({
          title: "Xa bi vit",
          text: action.payload.message,
          icon: "error",
          button: "OK ",
        });
      })
      .addCase(uploadImages.fulfilled, (state, action) => {
        // Nu thm bi vit thnh cng
        if (action.payload.status === "OK") {
          state.nameFileUpload= action.payload.data
        }
      })
  },
});
export default postsSlice;

export const fetchPosts = createAsyncThunk(
  "posts/fetchPosts",
  async (params) => {
    const response = await postsApi.getAll(params);
    return response.data.data;
  }
);
export const addPost = createAsyncThunk("posts/addPost", async (postInfo) => {
  const response = await postsApi.add(postInfo);
  return response.data;
});
export const updatePost = createAsyncThunk(
  "posts/updatePost",
  async (postInfo) => {
    const response = await postsApi.update(postInfo);
    return [response.data, postInfo.id];
  }
);
export const deletePost = createAsyncThunk(
  "posts/deletePost",
  async (postId) => {
    const response = await postsApi.delete(postId);
    return [response.data, postId];
  }
);
export const uploadImages = createAsyncThunk("posts/uploadImages", async (data) => {
  const response = await postsApi.uploadImages(data);
  return response.data;
});